.loader{
    width: 100px;
    height: 100px;
    border: 5px dashed black;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 50px;
    animation: loading 1s infinite linear;
}

@keyframes loading{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}